// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
  font-size: ($sizeValue * 16) * 1px;
  font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Flex Justify Center Align Center
@mixin flex-jcenter-acenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
