/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  border: none;
  font-family: $font__headings;
  text-transform: uppercase;
  background-color: $color__brand-green;
  color: white;
  @include font-size(0.75);
  line-height: 1;
  padding: 0.6rem 1rem 0.4rem;
  &:hover {
    background-color: #19625d;
  }

  &:active,
  &:focus {
    border-color: #aaa #bbb #bbb;
    box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5),
      inset 0 2px 5px rgba(0, 0, 0, 0.15);
  }
}

/* Fields */

input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
textarea {
  color: #666;
  background-color: $color__background-light-grey;
  border: 1px solid #ccc;
  &:focus {
    color: #111;
  }
  label{
    text-transform: uppercase;
  }
}

input[type='text'],
input[type='email'],
input[type='url'],
input[type='password'],
input[type='search'] {
  padding: 3px;
}

textarea {
  padding-left: 3px;
  width: 100%;
}

// Search form
input[type='search']{
  background-color: transparent;
  border: none;
  font-family: $font__headings;
  padding: 0 .4rem 0 1rem;
  color: $color__brand-light-grey;
  &::placeholder {
    color: $color__brand-light-grey;
  }
}

.main-navigation .search-form 
  a{
  display: inline;
  padding: 0 1rem;
}

.header-with-hero{
  .search-form {
    input[type='search']{
      color: $color__brand-yellow;
      &::placeholder {
        color: $color__brand-yellow;
      }
    }
  }
}

.search-field-hidden{
  transition-duration: 1s;
  transition-property: all;
  transition-timing-function: ease-in-out;
  width: 0;
}

.search-field-show{
  transition-duration: 1s;
  transition-property: all;
  transition-timing-function: ease-in-out;
  width: 15rem;
}